import React from 'react';
import Navbar from '../components/Navbar';
import { data } from '../Data';
import { Link, useParams } from "react-router-dom";
import LineChart from '../components/LineChart';
import DoughnutChart from '../components/DoughnutChart';
import BarChart from '../components/BarChart';

export default function View() {
    const {id} = useParams();
    return (
        <React.Fragment>
            <Navbar />
            <div className="container" id="container">
                <div className="main">
                    <div className="section">
                        <Link to="/">
                            <div className="back-nav">
                                <span className="back">
                                    <i className="fas fa-chevron-left"></i>
                                </span>
                            </div>
                        </Link>
                        
                        <div className="card-wrapper">
                            <div className="card">
                                <div className="card-inner">
                                    <div className="event-header">
                                    </div>
                                    <div className="event-body">
                                        <img className="event-picture" src={data[id].img} alt="event"/>
                                        <div className="event-wrapper"> 
                                            <div className="event-title">
                                                {data[id].title}
                                            </div>
                                            <div className="event-subtitle">
                                                {data[id].date}
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>

                        <div className="subtitle">
                            Overview
                        </div>

                        <div className="card-wrapper">
                            <div className="card">
                                <div className="card-inner">
                                    <div className="card-header">
                                        Total page view
                                    </div>
                                    <div className="card-body large" id="displayPageview">
                                        800.000
                                    </div>
                                    <div className="card-footer">
                                        Since 6 months
                                    </div>
                                </div>
                            </div>
                            <div className="card">
                                <div className="card-inner">
                                    <div className="card-header">
                                        Total user
                                    </div>
                                    <div className="card-body large" id="displayUser">
                                        80.000
                                    </div>
                                    <div className="card-footer">
                                        Since 6 months
                                    </div>
                                </div>
                            </div>
                            <div className="card">
                                <div className="card-inner">
                                    <div className="card-header">
                                        Total session
                                    </div>
                                    <div className="card-body large" id="displayTotalSession">
                                        32.500
                                    </div>
                                    <div className="card-footer">
                                        Since 6 months
                                    </div>
                                </div> 
                            </div>
                            <div className="card">
                                <div className="card-inner">
                                    <div className="card-header">
                                        Total avg visit time
                                    </div>
                                    <div className="card-body large" >
                                        <span id="displayAvgVisit">160</span> S
                                    </div>
                                    <div className="card-footer">
                                        Since 6 months
                                    </div>
                                </div>
                            </div>
                        </div>

                        <div className="subtitle">
                            User Behaviour
                        </div>
            
                        <div className="card-wrapper">
                            <div className="card chart">
                                <div className="card-inner">
                                    <div className="card-header">
                                        Page view
                                    </div>
                                    <div className="card-body">
                                        <div>
                                            <LineChart />
                                        </div>
                                    </div>
                                    <div className="card-footer">
                                    </div>
                                </div>
                            </div>
                            <div className="card chart">
                                <div className="card-inner">
                                    <div className="card-header">
                                        User
                                    </div>
                                    <div className="card-body">
                                        <div>
                                            <LineChart />
                                        </div>
                                    </div>
                                    <div className="card-footer">
                                    </div>
                                </div>
                            </div>
                        </div>

                        <div className="card-wrapper">
                            <div className="card chart">
                                <div className="card-inner">
                                    <div className="card-header">
                                        Session
                                    </div>
                                    <div className="card-body">
                                        <div>
                                            <LineChart />
                                        </div>
                                    </div>
                                    <div className="card-footer">
                                    </div>
                                </div>
                            </div>
                            <div className="card chart">
                                <div className="card-inner">
                                    <div className="card-header">
                                        Avg visit time
                                    </div>
                                    <div className="card-body">
                                        <div>
                                            <LineChart />
                                        </div>
                                    </div>
                                    <div className="card-footer">
                                    </div>
                                </div>
                            </div>
                        </div>

                        <div className="card-wrapper">
                            <div className="card chart">
                                <div className="card-inner">
                                    <div className="card-header">
                                        User devices
                                    </div>
                                    <div className="card-body">
                                        <div>
                                            <DoughnutChart />
                                        </div>
                                    </div>
                                    <div className="card-footer">
                                    </div>
                                </div>
                            </div>
                            <div className="card chart">
                                <div className="card-inner">
                                    <div className="card-header">
                                        Top country
                                    </div>
                                    <div className="card-body">
                                        <div>
                                            <BarChart />
                                        </div>
                                    </div>
                                    <div className="card-footer">
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </React.Fragment>
    )
}
