import React, { useState, useContext } from 'react';
import { AuthContext } from '../context/AuthContext';

export default function Navbar() {
    const authContext = useContext(AuthContext);
    const [sidebar, setSidebar] = useState(false);
    const [dropdownMenu, setDropdownMenu] = useState(false);

    const toggler = function() { 
        setSidebar(!sidebar)
    }

    const menuToggler = function() {
        setDropdownMenu(!dropdownMenu)
    }

    const logout = () => {
        authContext.logout();
    };

    return (
        <React.Fragment>
            <nav>
                <button type="button" className="navbar-toggler" id="navbar-toggler" onClick={toggler}>
                    <div className="toggler-icon"></div>
                    <div className="toggler-icon"></div>
                    <div className="toggler-icon"></div>
                </button>
                <a href="/"><img className="navbar-logo" src="/assets/img/logo-vairbees.png" alt="logo"/></a>
                <div className='navbar-menu'>
                    <button className='btn-profile'><img className="navbar-profile" src="/assets/img/profile.png" alt="profile" onClick={menuToggler}/></button>
                    <div className={"dropdownMenu " + (dropdownMenu === true?"active" : "")}>
                        <button onClick={logout} className='dropdown btn-profile'><i className="fas fa-sign-out-alt"></i> Logout</button>
                    </div>
                </div>
            </nav>

            <div className={"sidebar " + (sidebar === true?"active" : "")} id="sidebar">
                <div className="role">
                    <div className="role-header">
                        <div style={{"fontSize": "15px"}}>Role</div>
                        <button className="role-button">CHANGE</button>
                    </div>
                    <div style={{"fontSize": "16px", "marginTop": "30px"}}>Staff</div>
                    <div style={{"fontSize": "14px"}}>Vairbees</div>
                    <div style={{"fontSize": "14px"}}>BINUS UNIVERSITY</div>
                </div>
                <ul>
                    <li className="nav-item"><a className="nav-link active" href="index.html">
                        <img className="icon" src="/assets/img/icon-dashboard.png" alt="icon"/>
                        Dashboard
                        </a>
                    </li>
                </ul>
            </div>
        </React.Fragment>
    )
}