import React, { useContext, useEffect, useRef } from 'react';
import Navbar from '../components/Navbar';
import { data } from '../Data';
import { Link } from "react-router-dom";
import { AuthContext } from '../context/AuthContext';
import { getGapiBatchReport } from '../services/GoogleService';

export default function Home() {
    const authContext = useContext(AuthContext);
    const loaded = useRef(false);

    useEffect(() => {
        if (!loaded.current && authContext.loaded.current) {
            loaded.current = true;
            if (!authContext.token) {
            }
        }
    }, [authContext.loaded]);

    /*const loadData = (token) => {
        if (token) {
            getGapiBatchReport(token, "default", {
                debug: true,
                callback: (response) => {
                    
                }
            })
        }
    }*/

    return (
        <React.Fragment>
            <Navbar />
            <div className="container" id="container">
                <div className="main">
                    <div className="section">
                        <div className="subtitle">
                            Overview
                        </div>

                        <div className="card-wrapper">
                            <div className="card">
                                <div className="card-inner">
                                    <div className="card-header">
                                        Total page view
                                    </div>
                                    <div className="card-body large" id="displayPageview">
                                        800.000
                                    </div>
                                    <div className="card-footer">
                                        Since 6 months
                                    </div>
                                </div>
                            </div>
                            <div className="card">
                                <div className="card-inner">
                                    <div className="card-header">
                                        Total user
                                    </div>
                                    <div className="card-body large" id="displayUser">
                                        80.000
                                    </div>
                                    <div className="card-footer">
                                        Since 6 months
                                    </div>
                                </div>
                            </div>
                            <div className="card">
                                <div className="card-inner">
                                    <div className="card-header">
                                        Total session
                                    </div>
                                    <div className="card-body large" id="displayTotalSession">
                                        32.500
                                    </div>
                                    <div className="card-footer">
                                        Since 6 months
                                    </div>
                                </div> 
                            </div>
                            <div className="card">
                                <div className="card-inner">
                                    <div className="card-header">
                                        Total avg visit time
                                    </div>
                                    <div className="card-body large" >
                                        <span id="displayAvgVisit">160</span> S
                                    </div>
                                    <div className="card-footer">
                                        Since 6 months
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>

                    <div className="section">
                        <div className="subtitle">
                            List of all events
                        </div>

                        <div className="card-wrapper">
                            <div className="card">
                                <div className="card-inner">
                                    <div className="card-search">
                                        <div className="entries">
                                            Show 
                                            <select className="form-select" defaultValue="10">
                                                <option value="10">10</option>
                                                <option value="25">25</option>
                                                <option value="50">50</option>
                                                <option value="100">100</option>
                                            </select>
                                            entries
                                        </div>
                                        <div className="search">
                                            <input className="form-control" type="search" placeholder="Search" name="search" />
                                            <button type="submit" className="search-button">
                                                <i className="fas fa-search"></i>
                                            </button>
                                        </div>
                                    </div>
                                    <div className="card-body list-events">
                                        <div className="table-wrapper">
                                            <table>
                                                <thead>
                                                    <tr>
                                                        <th>No</th>
                                                        <th>Pages</th>
                                                        <th>Date</th>
                                                        <th>Action</th>
                                                    </tr>
                                                </thead>
                                                <tbody>
                                                    {data.map((data) => (
                                                        <tr key = {data.id}>
                                                            <td >{data.id+1}</td>
                                                            <td>{data['title']}</td>
                                                            <td>{data['date']}</td>
                                                            <td><Link to={`event/${data.id}`}>View</Link></td>
                                                        </tr>
                                                    ))}
                                                    {!data.length && (
                                                        <tr>
                                                            <td>No data found</td>
                                                        </tr>
                                                    )}
                                                </tbody>
                                            </table> 
                                        </div>
                                    </div>
                                    <div className="card-footer view-all">
                                        VIEW ALL
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </React.Fragment>
    )
}
