import React, { useState, useEffect } from 'react';
import { Chart as ChartJS, CategoryScale,LineController, LineElement, PointElement, LinearScale, Title, Tooltip, Legend} from 'chart.js';
import { Line } from 'react-chartjs-2';

ChartJS.register(CategoryScale, LineController, LineElement, PointElement, LinearScale, Title, Tooltip, Legend);

function LineChart() {
    const [chartData, setChartData] = useState({
        datasets: [],
    });
    const [chartOptions, setChartOptions] = useState({});

    useEffect(() => {
        setChartData({
            labels: ['January', 'February', 'March', 'April','May', 'June'],
            datasets: [{
                label: "User",
                data: [12, 19, 3, 5, 2, 3],
                backgroundColor: 'rgb(241, 135, 0)',
                borderColor: 'rgb(241, 135, 0)',
                hoverOffset: 4
            }],
        });

        setChartOptions({
            responsive: true,
            plugins: {
                legend: {
                    display: false
                }
            },
            scales: {
                y: {
                    beginAtZero: true
                }
            }
        });
        
      }, []);

    return (
        <div>
            <Line data={chartData} options={chartOptions} />
        </div>
    )
}

export default LineChart;