import React, {useContext, useEffect, useState} from 'react'
import { AuthContext } from '../context/AuthContext';

export default function Login() {
  const authContext = useContext(AuthContext);

    useEffect(() => {
        if (authContext.token) {
            window.location.href = '/';
        }
    }, [authContext.token])

	/*useEffect(() => {
		window.google.accounts.id.initialize({
			client_id: CONSTANT.CLIENT_ID,
			callback: (response) => {
                authContext.login(response.credential);
            }
		});
		window.google.accounts.id.renderButton(
			document.getElementById("btn-access"), 
            {theme: "outline"}
		);

	},[]);*/

    const [errorMessages, setErrorMessages] = useState({});

    const error = "Invalid username or password";

    const database = [
        {
            username: "vairbees",
            password: "2035"
        }
      ];

    const handleSubmit = (event) => {
        event.preventDefault();
    
        var { uname, pass } = document.forms[0];
    
        const userData = database.find((user) => user.username === uname.value);
    
        if (userData) {
            if (userData.password !== pass.value) {
                setErrorMessages({ message: error });
            } 
            else {
                setErrorMessages({ message: "" });
                authContext.login("hello");
            }
        } 
        else {
            setErrorMessages({ message: error });
        }
    };

    const renderErrorMessage = () => (
        <div className="error">{errorMessages.message}</div>
    );

    return (
        <div className="main-login">
            <img className="logo" src="/assets/img/logo.svg" alt="logo"/>
            <form className='loginForm' onSubmit={handleSubmit}>
                <input id="username" type="text" name="uname" required placeholder='Username' autoFocus></input>
                <input id="password" type="password" name="pass" required placeholder='Password'></input>
                {renderErrorMessage()}
                <button type='submit'>Login</button>
            </form>
            
            <div id="btn-access"></div>
        </div>
    )
}
