import React, { useEffect, useRef, useState } from "react";
import { useNavigate } from "react-router-dom";

export const AuthContext = React.createContext();

export default function AuthProvider({ children }) {
    const [token, setToken] = useState(null);
    const loaded = useRef(false);
    //const navigate = useNavigate();

    useEffect(() => {
        if (!loaded.current) {
            loaded.current = true;
            const access_token = localStorage.getItem("VB_ACC_TOKEN") || null;
            if (access_token) {
                setToken(access_token);
            }
        }
    }, []);

    const login = (access_token) => {
        setToken(access_token);
        localStorage.setItem("VB_ACC_TOKEN", access_token);
    }

    const logout = () => {
        localStorage.removeItem("VB_ACC_TOKEN");
        //navigate('/login');
    }

    return (
        <AuthContext.Provider value={{token, setToken, login, logout, loaded}}>
            {children}
        </AuthContext.Provider>
    )
}